import React,{useState} from "react";
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import axios from "axios";
function CreateArea({setNotes}){
  const[isExpanded,setExpanded]=useState(false);
    const [note,set_note]=useState({
        title:"",
        content:""
     });
     function UpdateChange(event){
     const {name,value}=event.target
     set_note((prevNote)=> {return {...prevNote,[name]:value};});
     }
     function Submitnote(event){
         axios.post("/api/add", note)
         .then((res)=>{setNotes(res.data)});
         set_note({
             title:"",
             content:""
         })
        event.preventDefault();
     }
     function expand(){
       setExpanded(true);
     }
return (
        <div>
          <form className="create-note">
            {isExpanded && (<input onChange={UpdateChange} value={note.title} name="title" placeholder="Title" />)}
            <textarea onClick={expand} onChange={UpdateChange} value={note.content} name="content" placeholder="Take a note..." rows={isExpanded?3:1} />
            <Zoom in={true}>
            <Fab onClick={Submitnote}><AddIcon/></Fab>
            </Zoom>
          </form>
        </div>
);
}
export default CreateArea;